body {
  background-color: #d1a775;
  font-family: "Lato", sans-serif;
  font-size: 1.6rem;
}

[class$=contenedor],
.contenedor {
  width: 90%;
  max-width: 1200px;
  margin: 0 auto;
}
@media (min-width: 768px) {
  [class$=contenedor],
.contenedor {
    width: 80%;
  }
}
@media (min-width: 1024px) {
  [class$=contenedor],
.contenedor {
    width: 75%;
  }
}

img {
  max-width: 100%;
  display: block;
}

ul {
  padding: 0;
  list-style: none;
  margin: 0;
  color: #FFFFFF;
}

.seccion {
  padding: 8rem 0;
}

h1, h2, h3, h4 {
  margin: 0;
  line-height: 1.5;
}

h1 {
  font-size: 4rem;
}

h2 {
  font-size: 3.2rem;
}

h3 {
  font-size: 2.4rem;
}

a {
  text-decoration: none;
  color: #FFFFFF;
}

p {
  line-height: 1.3;
  font-size: 1.3rem;
  color: #FFFFFF;
  margin: 0;
}
@media (min-width: 768px) {
  p {
    font-size: 2rem;
  }
}

span {
  display: block;
  text-transform: uppercase;
  font-size: 1.4rem;
  font-weight: normal;
  font-weight: 700;
}
@media (min-width: 768px) {
  span {
    font-size: 2.3rem;
  }
}

.btn {
  background-color: #FFFFFF;
  padding: 2rem 2rem;
  color: #000;
  margin-top: 2rem;
  font-size: 2.2rem;
  font-weight: 400;
  display: inline-block;
  font-weight: 700;
  text-align: center;
  text-transform: uppercase;
  transition: background-color;
  transition-duration: 300ms;
}
@media (min-width: 768px) {
  .btn {
    width: auto;
  }
}
.btn:hover {
  background-color: #e6e6e6;
}

.boton-negro {
  background-color: #000;
  padding: 2rem 2rem;
  color: #FFFFFF;
  margin-top: 2rem;
  font-size: 2.2rem;
  font-weight: 400;
  display: inline-block;
  font-weight: 700;
  text-align: center;
  text-transform: uppercase;
  transition: background-color;
  transition-duration: 300ms;
}
@media (min-width: 768px) {
  .boton-negro {
    width: auto;
  }
}
.boton-negro:hover {
  background-color: black;
}

.header__logo {
  margin: 5rem auto;
}
.header__heading {
  text-align: center;
  color: #613434;
  padding: 0 3rem;
}
.header__heading span {
  display: block;
  font-size: 2rem;
}
.header__heading span::after, .header__heading span::before {
  content: " - ";
}

.menu__heading {
  color: #613434;
  text-align: center;
}
.menu__contenido {
  margin-top: 2rem;
  display: grid;
  gap: 1rem;
  grid-template-columns: repeat(3, 1fr);
  align-items: center;
}
.menu__listado {
  grid-column: 2/4;
}
.menu__titulo {
  margin: 0;
}
.menu__lista {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.menu__precio {
  font-weight: 900;
}

.especial blockquote {
  margin: 0 0 0.5rem 0;
  color: #FFFFFF;
}
.especial__marco {
  text-align: center;
  margin: 0.5rem 1.5rem;
  border: 7px solid #be982b;
}
.especial__texto {
  margin: 0.5rem;
}
.especial__precio span {
  font-size: 2rem;
}

.margin-bottom {
  margin-bottom: 2rem;
}

.heading-border {
  text-transform: uppercase;
  text-align: center;
  border-color: #613434;
  border-style: double;
  margin: 1rem auto;
  width: 20rem;
  color: #FFFFFF;
  font-size: 2rem;
}

.contacto {
  text-align: center;
  margin: 2rem 0;
}
.contacto__heading {
  color: #613434;
  margin-bottom: 0.5rem;
  text-transform: uppercase;
  font-weight: 900;
}
.contacto__telefono {
  font-size: 2rem;
}
.contacto__contenido {
  margin: 0 1.5rem;
}

.pago__flex {
  display: flex;
  align-items: center;
  width: 25rem;
  margin: 1.5rem auto 1rem auto;
}

.logo {
  width: 5rem;
  border-radius: 1rem;
  object-fit: cover;
}

.contenido {
  margin-top: 2rem;
  display: grid;
  gap: 1rem;
  grid-template-columns: repeat(3, 1fr);
  align-items: center;
}
.contenido__listado {
  grid-column: 1/3;
}

.imagen {
  min-height: 17rem;
  object-fit: cover;
}

.pizza__flex {
  display: flex;
  justify-content: space-between;
  margin-bottom: 1rem;
}
.pizza__tipo {
  text-align: center;
  text-transform: uppercase;
  background-color: #613434;
  border-radius: 1rem;
  padding: 0.5rem;
}
.pizza__grid {
  display: grid;
  gap: 3rem;
  grid-template-columns: repeat(2, 1fr);
  align-items: center;
}

.galeria__imagenes {
  display: grid;
  gap: 0.5rem;
  grid-template-columns: repeat(4, 1fr);
  margin: 2rem 0;
}
.galeria img {
  height: 100%;
  object-fit: cover;
}
.galeria__imagen:nth-child(1) {
  grid-row: 1/3;
}
.galeria__imagen:nth-child(2) {
  grid-column: 2/4;
}
.galeria__imagen:nth-child(3) {
  grid-column: 4/5;
}
.galeria__imagen:nth-child(4) {
  grid-row: 2/3;
}
.galeria__imagen:nth-child(5) {
  grid-column: 3/5;
}

.footer__copyright {
  text-align: center;
  color: #613434;
  font-size: 1.5rem;
  padding: 2rem;
}